import styled from 'styled-components';

export default styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 30%;
  flex-wrap: wrap;
  height: 100vh;
  padding: 70px 20px 20px;
  border-right: 1px solid darkcyan;
`;



