import styled from 'styled-components';

export default styled.div`
  position: relative;
  padding: 20px;
  margin: 0 auto 30px;
  background-color: #eee;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
`;



