import styled from 'styled-components';

export default styled.div`
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid white;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  a {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  img {
    width: 100%;
    object-fit: cover;
  }
`;



